import { createApp } from 'vue'
import { createStore } from "vuex";
import App from './App'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap"
import Notifications from '@kyvg/vue3-notification'
import tooltip from "@/directives/tooltip.js";
import "@/directives/tooltip.css";
const store = createStore({
    state: {
        current_user_commands: [],
        current_device: "",
        current_component: "Devices",
        page:"Auth",
        userList:[],
        devices:[],
        groups:[],
        currentDeviceData:[],
        ws_link:localStorage.default_ws_link?localStorage.default_ws_link:"wss://lora-ws.rossma.tk",
    },
    getters: {
        page: state => () => state.page,
        component: state => () => state.current_component,
        current_device: state => () => state.current_device,
        currentDeviceData: state => () => state.currentDeviceData,
        userList: state => () => state.userList,
        devices: state => () => state.devices,
        groups: state => () => state.groups,
        ws_link: state => () => state.ws_link,
    },
    mutations: {
        set_current_user_commands: (state, payload) => {
            state.current_user_commands = payload;
        },
        set_current_device: (state, payload) => {
            state.current_device = payload;
        },
        set_currentDeviceData: (state, payload) => {
            state.currentDeviceData = payload;
        },
        set_current_page: (state, payload) => { 
            state.page = payload;
        },
        set_current_component: (state, payload) => {
            state.current_component = payload;
        },
        set_userList: (state, payload) => {
            state.userList = payload;
        },
        set_devices: (state, payload) => {
            state.devices = payload;
        },
        set_groups: (state, payload) => {
            state.groups = payload;
        },
        set_ws_link: (state, payload) => {
            state.ws_link = payload;
        },
    }
});


createApp(App).use(store).use(Notifications).directive("tooltip", tooltip).mount('#app')

