import Devices from "@/components/Devices.vue";
import Users from "@/components/Users.vue";
import Device_detail from "@/components/Device_detail.vue";
import Decipher from "@/components/Decipher.vue";
import WS from "@/services/websocket";
export default {
  components: {
    Users,
    Devices,
    Device_detail,
    Decipher,
  },
  data() {
    return {
      component: "Devices",
      isActive: 1,
    };
  },
  mounted() {
    this.$store.watch(this.$store.getters.component, (component) => {
      if (component != "Auth") {
        this.component = component;
        if (this.component == "Devices") {
          this.isActive = 1;
        } else if (this.component == "Users") {
          this.isActive = 2;
        } else if (this.component == "Decipher") {
          this.isActive = 3;
        } else {
          this.isActive = 4;
        }
      }
    });
  },
  methods: {
    change_component(component) {
      this.$store.commit("set_current_component", component);
    },
    exit() {
      var command = JSON.stringify({
        cmd: "close_auth_req",
        token: localStorage.token,
      });
      WS.send(command);
    },
  },
};
</script>

