<template>
  <div
    v-if="loading"
    class="d-flex vh-100 justify-content-center align-items-center"
  >
    <div
      class="spinner-border"
      role="status"
      style="width: 5rem; height: 5rem"
    ></div>
  </div>
  <div v-else class="container-fluid data">
    <div class="row">
      <div class="d-flex flex-column col-2 offset-lg-1">
        <button
          type="button"
          class="btn btn-success shadow-none mb-3"
          data-bs-toggle="modal"
          data-bs-target="#add"
          v-on:click="newDevice"
        >
          Добавить новое устройство
        </button>

        <input
          type="text"
          placeholder="Поиск по названию"
          class="form-control shadow-none mb-2"
          v-model="search_input"
        />
        <div
          v-for="(group, key) in groups"
          v-bind:key="group.id"
          class="form-check"
        >
          <input
            class="form-check-input shadow-none"
            type="radio"
            v-bind:id="'flexRadioDefault' + key"
            v-bind:value="group"
            v-model="filter"
          />
          <label class="form-check-label" v-bind:for="'flexRadioDefault' + key">
            {{ group }}
          </label>
        </div>
      </div>
      <div class="col-lg-8 col-md-10">
        <table class="table table-bordered table-hover w-100">
          <thead class="sticky-top">
            <tr>
              <th scope="col">Название устройства</th>
              <th scope="col">DevEUI</th>
              <th scope="col">Группа</th>
              <th scope="col">Дата последней активности</th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="device in filtered_devices"
              v-bind:key="device.id"
              role="button"
            >
              <td v-on:click="setActive(device)">{{ device.devName }}</td>
              <td v-on:click="setActive(device)">{{ device.devEui }}</td>
              <td v-on:click="setActive(device)">{{ device.group }}</td>
              <td v-on:click="setActive(device)">
                {{
                  new Date(device.last_data_ts).toLocaleString("ru-RU", {
                    day: "numeric",
                    month: "long",
                    year: "numeric",
                  })
                }}
              </td>
              <td class="key">
                <button
                  type="button"
                  class="btn btn-secondary shadow-none"
                  title="Настроить устройство"
                  data-bs-toggle="modal"
                  data-bs-target="#add"
                  v-on:click="forDelete_Edit(device)"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-gear-fill"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M9.405 1.05c-.413-1.4-2.397-1.4-2.81 0l-.1.34a1.464 1.464 0 0 1-2.105.872l-.31-.17c-1.283-.698-2.686.705-1.987 1.987l.169.311c.446.82.023 1.841-.872 2.105l-.34.1c-1.4.413-1.4 2.397 0 2.81l.34.1a1.464 1.464 0 0 1 .872 2.105l-.17.31c-.698 1.283.705 2.686 1.987 1.987l.311-.169a1.464 1.464 0 0 1 2.105.872l.1.34c.413 1.4 2.397 1.4 2.81 0l.1-.34a1.464 1.464 0 0 1 2.105-.872l.31.17c1.283.698 2.686-.705 1.987-1.987l-.169-.311a1.464 1.464 0 0 1 .872-2.105l.34-.1c1.4-.413 1.4-2.397 0-2.81l-.34-.1a1.464 1.464 0 0 1-.872-2.105l.17-.31c.698-1.283-.705-2.686-1.987-1.987l-.311.169a1.464 1.464 0 0 1-2.105-.872l-.1-.34zM8 10.93a2.929 2.929 0 1 1 0-5.86 2.929 2.929 0 0 1 0 5.858z"
                    ></path>
                  </svg>
                  <span class="visually-hidden">Button</span>
                </button>
              </td>
              <td class="key">
                <button
                  type="button"
                  class="btn btn-danger shadow-none"
                  data-bs-toggle="modal"
                  data-bs-target="#delete"
                  title="Удалить устройство"
                  v-on:click="forDelete_Edit(device)"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-x-lg"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"
                    ></path>
                  </svg>
                  <span class="visually-hidden">Button</span>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="delete"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">Предупреждение</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          Вы действительно хотите удалить <b>{{ current_device.devName }}</b
          >?
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-danger" data-bs-dismiss="modal">
            Отмена
          </button>
          <button
            type="button"
            class="btn btn-success"
            data-bs-dismiss="modal"
            v-on:click="deleteDevice"
          >
            Подтвердить
          </button>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="add"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h4
            v-if="delete_or_edit"
            class="modal-title"
            id="staticBackdropLabel"
          >
            Редактирование настроек устройства
          </h4>
          <h4 v-else class="modal-title" id="staticBackdropLabel">
            Добавление устройства
          </h4>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row row-cols-1 row-cols-md-2 g-4">
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">
                  Activation by personalisation (ABP)
                </div>
                <div class="card-body">
                  <label for="devAddr" class="form-label">devAddr</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="devAddr"
                      class="form-control shadow-none"
                      placeholder="devAddr"
                      title="devAddr"
                      v-bind:disabled="delete_or_edit"
                      v-model="devAddr"
                    />
                  </div>
                  <label for="AppSkey" class="form-label">AppSkey</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="AppSkey"
                      class="form-control shadow-none"
                      placeholder="APPSKEY"
                      title="AppSKey"
                      v-bind:disabled="delete_or_edit"
                      v-model="AppSKey"
                    />
                  </div>
                  <label for="NwkSKey" class="form-label">NwkSKey</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="NwkSKey"
                      class="form-control shadow-none"
                      placeholder="NWKSKEY"
                      title="NwkSKey"
                      v-bind:disabled="delete_or_edit"
                      v-model="NwkSKey"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Over-the-air activation (OTAA)</div>
                <div class="card-body">
                  <label for="AppEUI" class="form-label">AppEUI</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="AppEUI"
                      class="form-control shadow-none"
                      placeholder="APPEUI"
                      title="AppEUI"
                      v-bind:disabled="delete_or_edit"
                      v-model="AppEui"
                    />
                  </div>
                  <label for="AppKey" class="form-label">AppKey</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="AppKey"
                      class="form-control shadow-none"
                      placeholder="APPKEY"
                      title="AppKey"
                      v-bind:disabled="delete_or_edit"
                      v-model="AppKey"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Основные настройки</div>
                <div class="card-body">
                  <label for="devName" class="form-label"
                    >Название устройства</label
                  >
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="devName"
                      class="form-control shadow-none"
                      placeholder="Название устройства"
                      title="Название устройства"
                      v-bind:disabled="delete_or_edit"
                      v-model="devName"
                    />
                  </div>
                  <label for="devEUI" class="form-label"
                    >End-device identifier (DevEUI)</label
                  >
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="devEUI"
                      class="form-control shadow-none"
                      placeholder="devEUI"
                      title="devEUI"
                      v-bind:disabled="delete_or_edit"
                      v-model="devEui"
                    />
                  </div>
                  <label for="classSelect" class="form-label">Класс</label>
                  <div class="input-group mb-3">
                    <select
                      class="form-select shadow-none"
                      v-model="devClass"
                      v-bind:disabled="!delete_or_edit"
                      id="classSelect"
                    >
                      <option value="CLASS_A">Class A</option>
                      <option value="CLASS_C">Class C</option>
                    </select>
                  </div>
                  <label for="group" class="form-label">Группа</label>
                  <div class="input-group mb-3">
                    <input
                      id="group"
                      class="form-control shadow-none"
                      list="datalistOptions"
                      placeholder="Группа"
                      v-model="group"
                    />
                    <datalist id="datalistOptions">
                      <option
                        v-for="group in groups"
                        v-bind:value="group"
                        v-bind:key="group.id"
                      ></option>
                    </datalist>
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Настройки частоты</div>
                <div class="card-body">
                  <label for="freqSelect" class="form-label"
                    >Частотный план</label
                  >
                  <div class="input-group mb-3">
                    <select
                      class="form-select shadow-none"
                      id="freqSelect"
                      v-model="freqPlan"
                    >
                      <option>EU 868</option>
                      <option>RU 868</option>
                      <option>Настраиваемый</option>
                    </select>
                  </div>
                  <table class="table">
                    <thead>
                      <tr>
                        <th scope="col">№</th>
                        <th scope="col">Частота</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th scope="row">freq4</th>
                        <td>
                          <input
                            type="text"
                            class="form-control shadow-none"
                            v-model="freq4"
                            v-bind:disabled="
                              freqPlan == 'RU 868' || freqPlan == 'EU 868'
                            "
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">freq5</th>
                        <td>
                          <input
                            type="text"
                            class="form-control shadow-none"
                            v-model="freq5"
                            v-bind:disabled="
                              freqPlan == 'RU 868' || freqPlan == 'EU 868'
                            "
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">freq6</th>
                        <td>
                          <input
                            type="text"
                            class="form-control shadow-none"
                            v-model="freq6"
                            v-bind:disabled="
                              freqPlan == 'RU 868' || freqPlan == 'EU 868'
                            "
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">freq7</th>
                        <td>
                          <input
                            type="text"
                            class="form-control shadow-none"
                            v-model="freq7"
                            v-bind:disabled="
                              freqPlan == 'RU 868' || freqPlan == 'EU 868'
                            "
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">freq8</th>
                        <td>
                          <input
                            type="text"
                            class="form-control shadow-none"
                            v-model="freq8"
                            v-bind:disabled="
                              freqPlan == 'RU 868' || freqPlan == 'EU 868'
                            "
                          />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                  <label for="RX2" class="form-label">RX2 частота, Гц</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="RX2"
                      class="form-control shadow-none"
                      placeholder="RX2"
                      title="RX2"
                      v-bind:disabled="
                        freqPlan == 'RU 868' || freqPlan == 'EU 868'
                      "
                      v-model="freqRx2"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-danger"
            ref="close"
            data-bs-dismiss="modal"
          >
            Отмена
          </button>
          <button
            v-if="delete_or_edit"
            type="button"
            class="btn btn-success"
            v-on:click="save_settings"
            data-bs-dismiss="modal"
          >
            Сохранить
          </button>
          <button
            v-else
            type="button"
            class="btn btn-success"
            v-on:click="save_settings"
            data-bs-dismiss="modal"
          >
            Подтвердить
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import WS from "@/services/websocket";
export default {
  components: {},
  data() {
    return {
      devices: [],
      filtered_devices: [],
      groups: [],
      filter: "",
      search_input: "",
      current_device: [],
      delete_or_edit: false,
      devices_extend: [],
      devices_pre: {},
      loading: true,

      devAddr: "",
      AppSKey: "",
      NwkSKey: "",
      devName: "",
      devEui: "",
      devClass: "",
      group: "",
      AppEui: "",
      AppKey: "",

      freqPlan: "",
      freqPlans: {
        "RU 868": {
          freq4: 864100000,
          freq5: 864300000,
          freq6: 864500000,
          freq7: 864700000,
          freq8: 864900000,
        },
        "EU 868": {
          freq4: 867100000,
          freq5: 867300000,
          freq6: 867500000,
          freq7: 867700000,
          freq8: 867900000,
        },
        Настраиваемый: {
          freq4: 867100000,
          freq5: 867300000,
          freq6: 867500000,
          freq7: 867700000,
          freq8: 867900000,
        },
      },
      freq4: "",
      freq5: "",
      freq6: "",
      freq7: "",
      freq8: "",
      freqRx2: "",
    };
  },
  mounted() {
    this.getDeviceListExtended();
    this.getDeviceList();
    this.$store.watch(this.$store.getters.devices, (status) => {
      this.devices = status;
      this.filtered_devices = this.devices;
    });
    this.$store.watch(this.$store.getters.groups, (status) => {
      this.groups = status;
      this.filter = this.groups[0];
      this.loading = false;
    });
  },
  methods: {
    setActive(device) {
      this.$store.commit("set_current_device", device);
      this.$store.commit("set_current_component", "Device_detail");
    },
    getDeviceList() {
      var command = JSON.stringify({
        cmd: "get_device_appdata_req",
      });
      WS.send(command);
    },
    getDeviceListExtended() {
      var command = JSON.stringify({
        cmd: "get_devices_req",
      });
      WS.send(command);
    },
    forDelete_Edit(device) {
      this.current_device = device;
      try {
        this.devAddr = device.ABP.devAddress;
      } catch {
        this.devAddr = "";
      }
      try {
        this.AppSKey = device.ABP.appsKey;
      } catch {
        this.AppSKey = "";
      }
      try {
        this.NwkSKey = device.ABP.nwksKey;
      } catch {
        this.NwkSKey = "";
      }
      try {
        this.devName = device.devName;
      } catch {
        this.devName = "";
      }
      try {
        this.devEui = device.devEui;
      } catch {
        this.devEui = "";
      }
      try {
        this.devClass = device.class;
      } catch {
        this.devClass = "";
      }
      try {
        this.group = device.group;
      } catch {
        this.group = "";
      }
      try {
        this.AppEui = device.OTAA.appEui;
      } catch {
        this.AppEui = "";
      }
      try {
        this.AppKey = device.OTAA.appKey;
      } catch {
        this.AppKey = "";
      }
      try {
        this.freqRx2 = device.freqRx2;
      } catch {
        this.freqRx2 = 0;
      }
      try {
        this.freqPlan = "";
        this.freq4 = "";
        this.freq5 = "";
        this.freq6 = "";
        this.freq7 = "";
        this.freq8 = "";
        for (const [key, value] of Object.entries(this.freqPlans)) {
          if (
            JSON.stringify(this.freqPlans[key]) ===
            JSON.stringify(device.frequencyPlan)
          ) {
            this.freq4 = parseInt(this.freqPlans[key]["freq4"]);
            this.freq5 = parseInt(this.freqPlans[key]["freq5"]);
            this.freq6 = parseInt(this.freqPlans[key]["freq6"]);
            this.freq7 = parseInt(this.freqPlans[key]["freq7"]);
            this.freq8 = parseInt(this.freqPlans[key]["freq8"]);
            this.freqPlan = key;
            break;
          }
        }
      } catch {}
      this.delete_or_edit = true;
    },
    newDevice() {
      this.devAddr = "";
      this.AppSKey = "";
      this.NwkSKey = "";
      this.devName = "";
      this.devEui = "";
      this.devClass = "";
      this.group = "";
      this.AppEui = "";
      this.AppKey = "";
      this.freqPlan = "Настраиваемый";
      this.delete_or_edit = false;
    },
    deleteDevice() {
      var command = JSON.stringify({
        cmd: "delete_devices_req",
        devices_list: [this.current_device.devEui],
      });
      console.log(JSON.parse(command));
    },
    save_settings() {
      let dev_list = {
        devEui: this.devEui,
        devName: this.devName,
        ABP: {
          devAddress: parseInt(this.devAddr),
          appsKey: this.AppSKey,
          nwksKey: this.NwkSKey,
        },
        OTAA: {
          appEui: this.AppEui,
          appKey: this.AppKey,
        },
        frequencyPlan: {
          freq4: this.freq4,
          freq5: this.freq5,
          freq6: this.freq6,
          freq7: this.freq7,
          freq8: this.freq8,
        },
        class: this.devClass,
        channelMask: {
          channel1En: true,
          channel2En: true,
          channel3En: true,
          channel4En: true,
          channel5En: true,
          channel6En: true,
          channel7En: true,
          channel8En: true,
          channel9En: false,
          channel10En: false,
          channel11En: false,
          channel12En: false,
          channel13En: false,
          channel14En: false,
          channel15En: false,
          channel16En: false,
        },
        rxWindow: 2,
        delayJoin1: 5,
        preferDr: 5,
        reactionTime: 1000,
      };
      for (const key in dev_list) {
        if (typeof dev_list[key] == "object") {
          for (const item in dev_list[key]) {
            if (dev_list[key][item] === "" || !dev_list[key][item]) {
              delete dev_list[key][item];
            }
          }
        }
      }
      for (const key in dev_list) {
        if (dev_list[key] === "" || !dev_list[key]) {
          delete dev_list[key];
        }
      }
      for (const key in dev_list) {
        if (typeof dev_list[key] == "object") {
          if(Object.keys(dev_list[key]).length==0)
          {
            delete dev_list[key];
          }
        }
      }
      var command = JSON.stringify({
        cmd: "manage_devices_req",
        devices_list: [dev_list],
      });

      this.$refs["close"].click();
      console.log(JSON.parse(command));
    },
  },
  watch: {
    freqPlan() {
      this.freq4 = this.freqPlans[this.freqPlan]["freq4"];
      this.freq5 = this.freqPlans[this.freqPlan]["freq5"];
      this.freq6 = this.freqPlans[this.freqPlan]["freq6"];
      this.freq7 = this.freqPlans[this.freqPlan]["freq7"];
      this.freq8 = this.freqPlans[this.freqPlan]["freq8"];
      if (this.freqPlan == "Настраиваемый") {
        this.freqRx2 = 869525000;
      }
    },
    filter() {
      var self = this;
      let perem = Array.from(this.devices);
      this.filtered_devices = perem.filter(function (el) {
        if (self.filter == "Все устройства") {
          return true;
        } else if (self.filter == "Без группы") {
          return el.group == undefined;
        } else {
          return el.group == self.filter;
        }
      });
      this.filtered_devices = this.filtered_devices.filter(function (el) {
        if (self.search_input == "") {
          return true;
        } else {
          return el.devName.startsWith(self.search_input);
        }
      });
    },
    search_input() {
      var self = this;
      let perem = Array.from(this.devices);
      this.filtered_devices = perem.filter(function (el) {
        if (self.filter == "Все устройства") {
          return true;
        } else if (self.filter == "Без группы") {
          return el.group == undefined;
        } else {
          return el.group == self.filter;
        }
      });
      this.filtered_devices = this.filtered_devices.filter(function (el) {
        if (self.search_input == "") {
          return true;
        } else {
          return el.devName.startsWith(self.search_input);
        }
      });
    },
  },
};
</script>

<style scoped>
.data {
  margin-top: 2px;
}
select {
