<template>
  <div
    v-if="loading"
    class="d-flex vh-100 justify-content-center align-items-center"
  >
    <div
      class="spinner-border"
      role="status"
      style="width: 5rem; height: 5rem"
    ></div>
  </div>
  <div v-else class="container-fluid data">
    <div class="row flex-lg-row flex-md-column">
      <div class="d-flex flex-column col-lg-2 col-md-5 col-sm-5 col-7 offset-lg-1">
        <button
          type="button"
          class="btn btn-success shadow-none mb-3"
          v-on:click="addUser"
          data-bs-toggle="modal"
          data-bs-target="#add"
        >
          Добавить нового пользователя
        </button>
      </div>
      <div class="col-lg-5 col-md-12">
        <table class="table table-hover w-100">
          <thead>
            <tr>
              <th scope="col">Имя пользователя</th>
              <th scope="col">Доступ к устройствам</th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="user in userList" v-bind:key="user.id" role="button">
              <td>{{ user.login }}</td>
              <td>
                {{ user.device_access == "FULL" ? "Полный" : "Частичный" }}
              </td>
              <td>
                <button
                  type="button"
                  class="btn btn-secondary shadow-none"
                  title="Настроить пользователя"
                  data-bs-toggle="modal"
                  data-bs-target="#add"
                  v-on:click="forDelete_Edit(user)"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-gear-fill"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M9.405 1.05c-.413-1.4-2.397-1.4-2.81 0l-.1.34a1.464 1.464 0 0 1-2.105.872l-.31-.17c-1.283-.698-2.686.705-1.987 1.987l.169.311c.446.82.023 1.841-.872 2.105l-.34.1c-1.4.413-1.4 2.397 0 2.81l.34.1a1.464 1.464 0 0 1 .872 2.105l-.17.31c-.698 1.283.705 2.686 1.987 1.987l.311-.169a1.464 1.464 0 0 1 2.105.872l.1.34c.413 1.4 2.397 1.4 2.81 0l.1-.34a1.464 1.464 0 0 1 2.105-.872l.31.17c1.283.698 2.686-.705 1.987-1.987l-.169-.311a1.464 1.464 0 0 1 .872-2.105l.34-.1c1.4-.413 1.4-2.397 0-2.81l-.34-.1a1.464 1.464 0 0 1-.872-2.105l.17-.31c.698-1.283-.705-2.686-1.987-1.987l-.311.169a1.464 1.464 0 0 1-2.105-.872l-.1-.34zM8 10.93a2.929 2.929 0 1 1 0-5.86 2.929 2.929 0 0 1 0 5.858z"
                    ></path>
                  </svg>
                  <span class="visually-hidden">Button</span>
                </button>
              </td>
              <td>
                <button
                  type="button"
                  class="btn btn-danger shadow-none"
                  data-bs-toggle="modal"
                  data-bs-target="#delete"
                  title="Удалить пользователя"
                  v-on:click="forDelete_Edit(user)"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-x-lg"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"
                    ></path>
                  </svg>
                  <span class="visually-hidden">Button</span>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="delete"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">Предупреждение</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          Вы действительно хотите удалить <b>{{ current_user.login }}</b
          >?
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-danger" data-bs-dismiss="modal">
            Отмена
          </button>
          <button
            type="button"
            class="btn btn-success"
            data-bs-dismiss="modal"
            v-on:click="deleteUser"
          >
            Подтвердить
          </button>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="add"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h4
            v-if="delete_or_edit"
            class="modal-title"
            id="staticBackdropLabel"
          >
            Редактирование настроек пользователя
          </h4>
          <h4 v-else class="modal-title" id="staticBackdropLabel">
            Добавление нового пользователя
          </h4>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row row-cols-1 row-cols-md-2 g-4">
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Информация о пользователе</div>
                <div class="card-body">
                  <label for="login" class="form-label">Логин</label>
                  <div class="input-group mb-3">
                    <input
                      type="text"
                      id="login"
                      class="form-control shadow-none"
                      placeholder="Логин"
                      required="true"
                      title="Логин"
                      v-bind:disabled="delete_or_edit"
                      v-model="login"
                    />
                  </div>
                  <label
                    v-if="!delete_or_edit"
                    for="password"
                    class="form-label"
                    >Пароль</label
                  >
                  <div v-if="!delete_or_edit" class="input-group mb-3">
                    <input
                      type="text"
                      id="password"
                      class="form-control shadow-none"
                      placeholder="Пароль"
                      required="true"
                      title="Пароль"
                      v-model="password"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Привилегии пользователя</div>
                <div class="card-body">
                  <label for="devAccess" class="form-label"
                    >Доступ к устройствам</label
                  >
                  <div class="input-group mb-3">
                    <select
                      id="devAccess"
                      class="form-select shadow-none"
                      v-model="devAccess"
                    >
                      <option value="FULL">Полный</option>
                      <option value="SELECTED">Частичный</option>
                    </select>
                  </div>

                  <div class="form-check">
                    <input
                      class="form-check-input shadow-none"
                      type="checkbox"
                      v-bind:checked="console_enabled"
                      v-on:click="console_enabled = !console_enabled"
                      id="flexCheckDefault"
                    />
                    <label class="form-check-label" for="flexCheckDefault">
                      Доступ к консоли
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card border-dark mb-3">
                <div class="card-header">Права доступа</div>
                <div class="card-body">
                  <p class="mt-0 mb-1">Общие</p>
                  <div
                    v-for="(value, key) in command_list_full.common"
                    v-bind:key="key"
                  >
                    <div class="form-check">
                      <input
                        class="form-check-input shadow-none"
                        type="checkbox"
                        value=""
                        v-bind:id="key"
                        v-bind:checked="command_list.indexOf(key) != -1"
                        v-on:click="add_or_delete_command(key)"
                      />
                      <label class="form-check-label" v-bind:for="key">
                        {{ value }}
                      </label>
                    </div>
                  </div>
                  <p class="mt-3 mb-1">Расширенные</p>
                  <div
                    v-for="(value, key) in command_list_full.external"
                    v-bind:key="key"
                  >
                    <div class="form-check">
                      <input
                        class="form-check-input shadow-none"
                        type="checkbox"
                        value=""
                        v-bind:id="key"
                        v-bind:checked="command_list.indexOf(key) != -1"
                        v-on:click="add_or_delete_command(key)"
                      />
                      <label class="form-check-label" v-bind:for="key">
                        {{ value }}
                      </label>
                    </div>
                  </div>
                  <p class="mt-3 mb-1">Настройки сети</p>
                  <div
                    v-for="(value, key) in command_list_full.network"
                    v-bind:key="key"
                  >
                    <div class="form-check">
                      <input
                        class="form-check-input shadow-none"
                        type="checkbox"
                        value=""
                        v-bind:id="key"
                        v-bind:checked="command_list.indexOf(key) != -1"
                        v-on:click="add_or_delete_command(key)"
                      />
                      <label class="form-check-label" v-bind:for="key">
                        {{ value }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col" v-if="devAccess == 'SELECTED'">
              <div class="card border-dark mb-3">
                <div class="card-header">Доступные устройства</div>
                <div class="card-body">
                  <div class="new_dev mb-2">
                    <input
                      type="text"
                      id="DEVEUI"
                      class="form-control shadow-none"
                      placeholder="DEVEUI"
                      title="DEVEUI"
                      v-model="newdevEui"
                    />
                    <button
                      class="btn btn-success ms-1 new_dev_eui"
                      v-on:click="add_device"
                    >
                      Добавить devEui
                    </button>
                  </div>
                  <div class="container">
                    <div class="row">
                      <div
                        class="col-5 me-auto mb-2 d-flex"
                        v-for="device in available_devices"
                        v-bind:key="device.id"
                      >
                        <div>
                          {{ device }}
                        </div>
                        <div class="ms-auto">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="24"
                            height="24"
                            fill="red"
                            class="bi bi-x"
                            viewBox="0 0 16 16"
                            type="button"
                            v-on:click="delete_device(device)"
                          >
                            <path
                              d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
                            ></path>
                          </svg>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-danger" ref="close" data-bs-dismiss="modal">
            Отмена
          </button>
          <button
            v-if="delete_or_edit"
            v-on:click="save_settings"
            class="btn btn-success"
          >
            Сохранить
          </button>
          <button
            v-else
            v-on:click="save_settings"
            class="btn btn-success"
          >
            Добавить
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import WS from "@/services/websocket";
import command_list_full from "@/services/command_list_full";
export default {
  data() {
    return {
      userList: [],
      current_user: "",
      command_list_full: command_list_full,
      loading: true,
      delete_or_edit: false,
      login: "",
      password: "",
      command_list: [],
      console_enabled: false,
      devAccess: "SELECTED",
      available_devices: [],
      newdevEui: "",
      edited: false,
    };
  },
  mounted() {
    this.getUsers();
    this.$store.watch(this.$store.getters.userList, (status) => {
      this.userList = status;
      this.loading = false;
    });
  },
  methods: {
    getUsers() {
      var command = JSON.stringify({
        cmd: "get_users_req",
      });
      WS.send(command);
    },
    forDelete_Edit(user) {
      this.current_user = user;
      this.delete_or_edit = true;
      this.login = user.login;
      this.command_list = Array.from(user.command_list);
      this.console_enabled = user.consoleEnable;
      this.devAccess = user.device_access;
      this.available_devices = Array.from(user.devEui_list);
    },
    deleteUser() {
      var command = JSON.stringify({
        cmd: "delete_users_req",
        user_list: [this.current_user.login],
      });
      WS.send(command);
      this.getUsers();
      console.log(JSON.parse(command));
    },
    addUser() {
      this.delete_or_edit = false;
      this.current_user = [];
      this.login = "";
      this.password = "";
      this.command_list = [];
      this.console_enabled = false;
      this.devAccess = "SELECTED";
      this.available_devices = [];
      this.edited = false;
    },
    add_or_delete_command(command) {
      if (this.command_list.indexOf(command) != -1) {
        //если она есть
        this.command_list.splice(this.command_list.indexOf(command), 1);
      } else {
        this.command_list.push(command);
      }
    },
    add_device() {
      this.available_devices.push(this.newdevEui);
      this.newdevEui = "";
    },
    delete_device(device) {
      this.available_devices.splice(this.available_devices.indexOf(device), 1);
    },
    save_settings() {
      var command = JSON.stringify({
        cmd: "manage_users_req",
        user_list: this.delete_or_edit?[
          {
            login: this.login,
            device_access: this.devAccess,
            consoleEnable: this.console_enabled,
            command_list: this.command_list,
            devEui_list: this.available_devices,
            rx_settings: {
              unsolicited: true,
              direction: "ALL",
              withMacCommands: true,
            },
          },
        ]:[
          {
            login: this.login,
            password: this.password,
            device_access: this.devAccess,
            consoleEnable: this.console_enabled,
            command_list: this.command_list,
            devEui_list: this.available_devices,
            rx_settings: {
              unsolicited: true,
              direction: "ALL",
              withMacCommands: true,
            },
          },
        ],
      });
      console.log(JSON.parse(command));
      WS.send(command);
      this.getUsers();
      this.$refs['close'].click();
    },
  },
};
</script>

<style scoped>
.data {
  margin-top: 2px;
}
.content {
  display: flex;
  flex-direction: row;
}
.commands {
  margin-left: 10px;
}
.editor {
  margin-left: 5px;
}
label {
  margin-left: 10px;
}
.new_dev {
  display: flex;
}
.new_dev_eui {
  width: 40%;
}
</style>