<template>
  <notifications position="top center" />
  <component v-bind:is="page"></component>
</template>

<script>
import Auth from "@/pages/Auth.vue";
import Main from "@/pages/Main.vue";
import WS from "@/services/websocket";
export default {
  data() {
    return {
      page: Auth,
      devices_extend: [],
      devices_pre: {},
      devices: [],
      groups: [],
    };
  },
  mounted() {
    var self = this;
    WS.onmessage = function (json) {
      var response = JSON.parse(json.data);
      if (response.cmd == "auth_resp") {
        if (response.status == true) {
          self.$store.commit(
            "set_current_user_commands",
            response.command_list
          );
          self.$store.commit("set_current_page", "Main");
          localStorage.token = response.token;
        } else {
          self.$notify({
            title: "Ошибка",
            text: "Неверный логин или пароль",
            type: "error",
          });
        }
      } else if (response.cmd == "token_auth_resp") {
        if (response.status == true) {
          localStorage.token = response.token;
          self.$store.commit("set_current_page", "Main");
        }
      } else if (response.cmd == "get_users_resp") {
        self.$store.commit("set_userList", response.user_list);
      } else if (response.cmd == "get_devices_resp") {
        self.devices_extend = [];
        self.groups = [];
        self.devices_pre = {};
        self.devices = [];
        self.devices_extend = response.devices_list;
        for (let index in self.devices_extend) {
          self.devices_pre[self.devices_extend[index].devEui] = Object.assign(
            {},
            self.devices_extend[index],
            { group: "" }
          );
        }
      } else if (response.cmd == "get_device_appdata_resp") {
        self.devices_extend = response.devices_list;
        for (let index in self.devices_extend) {
          self.devices_pre[self.devices_extend[index].devEui].group =
            self.devices_extend[index].group;
        }
        for (const [key, value] of Object.entries(self.devices_pre)) {
          self.devices.push(value);
        }
        self.groups.push("Все устройства");
        for (let index in self.devices) {
          if (
            self.groups.indexOf(self.devices[index].group, 0) == -1 &&
            self.devices[index].group != undefined &&
            self.devices[index].group != ""
          ) {
            self.groups.push(self.devices[index].group);
          }
        }

        if (self.groups.length == 2) {
          } else {
            self.groups.push("Без группы");
          }
        self.devices.sort(function (a, b) {
          return b.last_data_ts - a.last_data_ts;
        });
        self.$store.commit("set_devices", self.devices);
        self.$store.commit("set_groups", self.groups);
      } else if (response.cmd == "close_auth_resp") {
        if (response.status == true) {
          self.$store.commit("set_current_page", "Auth");
          self.$store.commit("set_current_component", "Devices");
          self.$store.commit("set_devices", []);
          self.$store.commit("set_groups", []);
          self.$store.commit("set_ws_link", "wss://lora-ws.rossma.tk");
          localStorage.removeItem("token"); //Чистим токен
        }
      } else if (response.cmd == "get_data_resp") {
        if (response.status == true) {
          self.$store.commit("set_currentDeviceData", response);
        }
      }
      else if(response.cmd=="delete_users_resp")
      {
        if(response.status==true)
        {
          self.$notify({
              title: "Подтверждение",
              text: "Пользователь успешно удалён",
              type: "success",
            });
        }
      }
      else if(response.cmd=="manage_users_resp")
      {
        if(response.status==true)
        {
          self.$notify({
              title: "Подтверждение",
              text: "Пользователь успешно добавлен",
              type: "success",
            });
        }
      }
    };

    this.$store.watch(this.$store.getters.page, (component) => {
      if (component == "Auth") {
        this.page = Auth;
      } else if (component == "Main") {
        this.page = Main;
      }
    });
    this.$store.watch(this.$store.getters.ws_link, (link) => {
      var self = this;
      WS.onmessage = function (json) {
        var response = JSON.parse(json.data);
        if (response.cmd == "auth_resp") {
          if (response.status == true) {
            self.$store.commit(
              "set_current_user_commands",
              response.command_list
            );
            self.$store.commit("set_current_page", "Main");
            localStorage.token = response.token;
          } else {
            self.$notify({
              title: "Ошибка",
              text: "Неверный логин или пароль",
              type: "error",
            });
          }
        } else if (response.cmd == "token_auth_resp") {
          if (response.status == true) {
            localStorage.token = response.token;
            self.$store.commit("set_current_page", "Main");
          }
        } else if (response.cmd == "get_users_resp") {
          self.$store.commit("set_userList", response.user_list);
        } else if (response.cmd == "get_devices_resp") {
          self.devices_extend = [];
          self.devices_pre = {};
          self.devices_extend = response.devices_list;
          for (let index in self.devices_extend) {
            self.devices_pre[self.devices_extend[index].devEui] = Object.assign(
              {},
              self.devices_extend[index],
              { group: "" }
            );
          }
        } else if (response.cmd == "get_device_appdata_resp") {
          self.devices_extend = response.devices_list;
          for (let index in self.devices_extend) {
            self.devices_pre[self.devices_extend[index].devEui].group =
              self.devices_extend[index].group;
          }
          self.devices = [];
          for (const [key, value] of Object.entries(self.devices_pre)) {
            self.devices.push(value);
          }
          self.groups = [];
          self.groups.push("Все устройства");
          for (let index in self.devices) {
            if (
              self.groups.indexOf(self.devices[index].group, 0) == -1 &&
              self.devices[index].group != undefined &&
              self.devices[index].group != ""
            ) {
              self.groups.push(self.devices[index].group);
            }
          }
          if (self.groups.length == 2) {
          } else {
            self.groups.push("Без группы");
          }
          self.devices.sort(function (a, b) {
            return b.last_data_ts - a.last_data_ts;
          });
          self.$store.commit("set_devices", self.devices);
          self.$store.commit("set_groups", self.groups);
        } else if (response.cmd == "close_auth_resp") {
          if (response.status == true) {
            self.$store.commit("set_current_page", "Auth");
            self.$store.commit("set_current_component", "Devices");
            self.$store.commit("set_devices", []);
            self.$store.commit("set_groups", []);
            self.$store.commit("set_ws_link", "wss://lora-ws.rossma.tk");
            localStorage.removeItem("token"); //Чистим токен
          }
        } else if (response.cmd == "get_data_resp") {
          if (response.status == true) {
            self.$store.commit("set_currentDeviceData", response);
          }
        }
      };
    });
  },
};
</script>

