<template>
  <div
    v-if="loading"
    class="d-flex vh-100 justify-content-center align-items-center"
  >
    <div
      class="spinner-border"
      role="status"
      style="width: 5rem; height: 5rem"
    ></div>
  </div>
  <div v-else class="container-fluid info">
    <div class="row">
      <div class="d-flex flex-column col-2">
        <button
          type="button"
          class="btn btn-secondary shadow-none"
          v-on:click="getBack"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            class="bi bi-arrow-left-circle-fill"
            viewBox="0 0 16 16"
          >
            <path
              d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z"
            ></path>
          </svg>
          Назад
        </button>
        <button
          class="btn btn-primary mt-1 shadow-none"
          v-on:click="tab = 'table'"
        >
          Таблица
        </button>
        <button
          class="btn btn-primary mt-1 shadow-none"
          v-on:click="tab = 'graph'"
        >
          Графики
        </button>
      </div>
      <div v-if="tab === 'table'" class="col-lg-8 col-md-10">
        <label for="period">Данные за</label>
        <select
          id="period"
          class="form-select shadow-none"
          v-model="date"
          style="width: 15rem"
        >
          <option value="today">Сегодня</option>
          <option value="week">Неделя</option>
          <option value="month">Месяц</option>
          <option value="three_month">3 месяца</option>
          <option value="half_year">Полгода</option>
          <option value="year">Год</option>
          <option value="all">Всё время</option>
        </select>
        <table class="table table-bordered">
          <thead>
            <tr>
              <th colspan="100%" class="text-center header">
                {{ current_device.devName }}
              </th>
            </tr>
            <tr>
              <th colspan="100%" style="word-break: break-all">
                Последний пакет:
                <span
                  v-tooltip:top="'Нажмите для копирования'"
                  class="packet"
                  type="button"
                  ref="for_copy"
                  v-on:click="copy"
                  >{{ last_packet.data }}</span
                >
              </th>
            </tr>
            <tr>
              <th colspan="100%">Тип устройства : {{ type }}</th>
            </tr>
            <tr v-if="!error">
              <th scope="col">#</th>
              <th
                v-for="header in currentDeviceData.headers"
                v-bind:key="header.id"
                scope="col"
              >
                {{ header }}
                <span v-if="has_tok && isTok(header)">
                  <select v-model="for_temp">
                    <option v-bind:value="1">ед</option>
                    <option v-bind:value="187.5">мА</option>
                  </select>
                </span>
              </th>
            </tr>
          </thead>
          <tbody v-if="!error">
            <tr
              v-for="(data, key) in currentDeviceData.data.filter(function (
                item
              ) {
                if (item.date_time >= date_filter_down) return item;
              })"
              v-bind:key="data.id"
            >
              <th scope="row">{{ key + 1 }}</th>
              <td v-for="(el, key) in data.data" v-bind:key="el.id">
                {{
                  !isNaN(el)
                    ? key.startsWith("tok") && for_temp === 187.5
                      ? Math.round(
                          (Math.round((el / for_temp) * 1000) / 1000) * 100
                        ) / 100
                      : el
                    : ""
                }}
              </td>
              <td>{{ new Date(data.date_time).toLocaleString("ru-RU") }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else-if="tab === 'graph'" class="col">
        <h1>Название устройства : {{ current_device.devName }}</h1>
        <label for="period">Данные за</label>
        <select
          id="period"
          class="form-select shadow-none"
          v-model="date_graph"
          style="width: 15rem"
        >
          <option value="today">Сегодня</option>
          <option value="week">Неделя</option>
          <option value="month">Месяц</option>
          <option value="three_month">3 месяца</option>
          <option value="half_year">Полгода</option>
          <option value="year">Год</option>
          <option value="all">Всё время</option>
        </select>
        <div
          v-if="!error"
          class="
            row
            row-cols-1
            row-cols-sm-1
            row-cols-md-1
            row-cols-lg-2
            row-cols-xl-2
          "
        >
          <div
            class="col p-2"
            v-for="graph in currentDeviceData.graphs"
            v-bind:key="graph.id"
          >
            <apexchart
              ref="realtimeChart"
              type="line"
              v-bind:options="graph.chartOptions"
              v-bind:series="graph.series"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import WS from "@/services/websocket";
import VueApexCharts from "vue3-apexcharts";
export default {
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      response: [],
      currentDeviceData: [],
      last_packet: "",
      type: "",
      current_device: this.$store.state.current_devEui,
      loading: true,
      for_temp: 1,
      has_tok: false,
      tab: "table",
      error: false,
      date_filter_down: 0,
      date: "month",
      date_graph: "month",
      graphs: [],
    };
  },
  activated() {
    this.loading = true;
    this.error = false;
    this.for_temp = 1;
    this.tab = "table";
    this.date = "month";
    this.date_graph = "month";
    var today = new Date();
    today.setHours(0);
    today.setMinutes(0);
    today.setSeconds(0);
    today.setMilliseconds(0);
    this.date_filter_down=today.setMonth(today.getMonth()-1);
    this.current_device = this.$store.state.current_device;
    const unwatch = this.$store.watch(
      this.$store.getters.currentDeviceData,
      (status) => {
        this.response = status;
        if (this.response.data_list[0] != undefined) {
          this.last_packet = this.response.data_list[0];
        } else {
          this.last_packet = { data: "Нет пакета" };
          this.error = true;
        }
        this.currentDeviceData = this.parse(this.response.data_list);
        this.filter_data();
        this.loading = false;
        unwatch();
      }
    );
    this.get_data();
  },
  methods: {
    filter_data() {
      let result = [];
      for (const graph of JSON.parse(JSON.stringify(this.graphs))) {
        let chartOptions = graph.chartOptions;
        let series = graph.series;

        let xaxis = chartOptions.xaxis;
        let categories = xaxis.categories;

        let data = series[0].data;

        let promezh = [];
        data.map(function (e, i) {
          promezh.push({ var: e, date_time: categories[i] });
        });
        var today = new Date();
        today.setHours(0);
        today.setMinutes(0);
        today.setSeconds(0);
        today.setMilliseconds(0);
        if (this.date_graph == "today") {
        } else if (this.date_graph == "week") {
          today.setDate(today.getDate() - 7);
        } else if (this.date_graph == "month") {
          today.setMonth(today.getMonth() - 1);
        } else if (this.date_graph == "three_month") {
          today.setMonth(today.getMonth() - 3);
        } else if (this.date_graph == "half_year") {
          today.setMonth(today.getMonth() - 6);
        } else if (this.date_graph == "year") {
          today.setYear(today.getYear() - 1);
        } else if (this.date_graph == "all") {
          today = 0;
        }
        let result_data = [];
        let result_date_time = [];
        for (const item of promezh) {
          let splitter = item.date_time.split(", ");
          let date = splitter[0].split(".");
          date = date[2] + "-" + date[1] + "-" + date[0];
          splitter = date + "T" + splitter[1];
          if (Date.parse(splitter) >= today) {
            result_data.push(item.var);
            result_date_time.push(item.date_time);
          }
        }

        series[0].data = result_data;
        xaxis.categories = result_date_time;
        chartOptions.xaxis = xaxis;

        result.push({ series: series, chartOptions: chartOptions });
      }
      this.currentDeviceData.graphs = result;
    },
    get_data() {
      var command = JSON.stringify({
        cmd: "get_data_req",
        devEui: String(this.current_device.devEui),
      });
      WS.send(command);
    },
    getBack() {
      this.$store.commit("set_current_component", "Devices");
    },
    copy() {
      navigator.clipboard.writeText(this.$refs.for_copy.innerHTML);
      this.$notify({
        text: "Скопировано!",
        type: "success",
      });
    },
    isTok(header) {
      return header.startsWith("Ток");
    },
    parse(data) {
      let type = "";
      try {
        type = this.get_type(data[0].data);
        this.type = type;
        let final_result = {};
        let result = [];
        let headers = [];
        if (type == "ROSSMA IIOT-AMS ANALOG (multichannel)") {
          headers = [
            "Ток 1",
            "Ток 2",
            "Ток 3",
            "Напряжение, мВ",
            "Температура, °С",
          ];
          let date_times = []; /////////////////////////////////
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let tok_1 = parseInt(varka.slice(1, 2 + 1).join(""), 16);
            let tok_2 = parseInt(varka.slice(3, 4 + 1).join(""), 16);
            let tok_3 = parseInt(varka.slice(5, 6 + 1).join(""), 16);
            let volt = parseInt(varka.slice(7, 8 + 1).join(""), 16);
            let temp = parseInt(varka.slice(9, 10).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              tok_1: tok_1,
              tok_2: tok_2,
              tok_3: tok_3,
              volt: volt,
              temp: temp,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let tok_1_graph = [];
          let tok_2_graph = [];
          let tok_3_graph = [];
          let volt_graph = [];
          let temp_graph = [];
          for (let el of for_graph) {
            tok_1_graph.push(el.data.tok_1);
            tok_2_graph.push(el.data.tok_2);
            tok_3_graph.push(el.data.tok_3);
            volt_graph.push(el.data.volt);
            temp_graph.push(el.data.temp);
          }
          let graphics = {
            "График изменения тока 1": { vel_name: "Ток", graph: tok_1_graph },
            "График изменения тока 2": { vel_name: "Ток", graph: tok_2_graph },
            "График изменения тока 3": { vel_name: "Ток", graph: tok_3_graph },
            "График изменения напряжения": {
              vel_name: "Напряжение",
              graph: volt_graph,
            },
            "График изменения температуры": {
              vel_name: "Температура",
              graph: temp_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          }; /////////////////////////////////
          this.has_tok = true;
        } else if (type == "ROSSMA IIOT-AMS ANALOG (single channel)") {
          headers = ["Ток 1", "Напряжение, мВ", "Температура, °С"];
          let date_times = [];
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let tok_1 = parseInt(varka.slice(1, 2 + 1).join(""), 16);
            let volt = parseInt(varka.slice(3, 4 + 1).join(""), 16);
            let temp = parseInt(varka.slice(5, 6).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              tok_1: tok_1,
              volt: volt,
              temp: temp,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let tok_1_graph = [];
          let volt_graph = [];
          let temp_graph = [];
          for (let el of for_graph) {
            tok_1_graph.push(el.data.tok_1);
            volt_graph.push(el.data.volt);
            temp_graph.push(el.data.temp);
          }
          let graphics = {
            "График изменения тока 1": { vel_name: "Ток", graph: tok_1_graph },
            "График изменения напряжения": {
              vel_name: "Напряжение",
              graph: volt_graph,
            },
            "График изменения температуры": {
              vel_name: "Температура",
              graph: temp_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          };
          this.has_tok = true;
        } else if (type == "ROSSMA IIOT-AMS DRY CONTACT RELAY") {
          headers = [
            "Состояние кнопки старт",
            "Состояние кнопки стоп",
            "Состояние датчика двери",
            "Состояние датчика тока",
            "Состояние реле",
          ];
          let date_times = [];
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let start = parseInt(varka.slice(1, 1 + 1).join(""), 16);
            let stop = parseInt(varka.slice(2, 2 + 1).join(""), 16);
            let door = parseInt(varka.slice(3, 3 + 1).join(""), 16);
            let amper = parseInt(varka.slice(4, 4 + 1).join(""), 16);
            let relay = parseInt(varka.slice(5, 5 + 1).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              start: start,
              stop: stop,
              door: door,
              amper: amper,
              relay: relay,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let start_graph = [];
          let stop_graph = [];
          let door_graph = [];
          let amper_graph = [];
          let relay_graph = [];
          for (let el of for_graph) {
            start_graph.push(this.checker(el.data.start));
            stop_graph.push(this.checker(el.data.stop));
            door_graph.push(this.checker(el.data.door));
            amper_graph.push(this.checker(el.data.amper));
            relay_graph.push(this.checker(el.data.relay));
          }
          let graphics = {
            "Состояние кнопки старт": {
              vel_name: "Состояние",
              graph: start_graph,
            },
            "Состояние кнопки стоп": {
              vel_name: "Состояние",
              graph: stop_graph,
            },
            "Состояние датчика двери": {
              vel_name: "Состояние",
              graph: door_graph,
            },
            "Состояние датчика тока": {
              vel_name: "Состояние",
              graph: amper_graph,
            },
            "Состояние реле": {
              vel_name: "Состояние",
              graph: relay_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          }; /////////////////////////////////
        } else if (type == "ROSSMA IIOT-AMS TILT COUNTER") {
          headers = [
            "Число качаний с момента отправки. Датчик 1",
            "Число изменений состояния с момента отправки без обработки. Датчик 1",
            "Число качаний. Датчик 1",
            "Число изменений состояния без обработки. Датчик 1",
            "Число качаний с момента отправки. Датчик 2",
            "Число изменений состояния с момента отправки без обработки. Датчик 2",
            "Число качаний. Датчик 2",
            "Число изменений состояния без обработки. Датчик 2",
            "Напряжение питания в мВ",
            "Температура",
          ];
          let date_times = [];
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let count_from_send_1 = parseInt(
              varka.slice(1, 2 + 1).join(""),
              16
            );
            let count_change_send_no_obr_1 = parseInt(
              varka.slice(3, 4 + 1).join(""),
              16
            );
            let count_1 = parseInt(varka.slice(5, 12 + 1).join(""), 16);
            let count_change_no_obr_1 = parseInt(
              varka.slice(13, 20 + 1).join(""),
              16
            );
            let count_from_send_2 = parseInt(
              varka.slice(21, 22 + 1).join(""),
              16
            );
            let count_change_send_no_obr_2 = parseInt(
              varka.slice(23, 24 + 1).join(""),
              16
            );
            let count_2 = parseInt(varka.slice(25, 32 + 1).join(""), 16);
            let count_change_no_obr_2 = parseInt(
              varka.slice(33, 40 + 1).join(""),
              16
            );
            let volt = parseInt(varka.slice(41, 42 + 1).join(""), 16);
            let temp = parseInt(varka.slice(43, 43 + 1).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              count_from_send_1: count_from_send_1,
              count_change_send_no_obr_1: count_change_send_no_obr_1,
              count_1: count_1,
              count_change_no_obr_1: count_change_no_obr_1,
              count_from_send_2: count_from_send_2,
              count_change_send_no_obr_2: count_change_send_no_obr_2,
              count_2: count_2,
              count_change_no_obr_2: count_change_no_obr_2,
              volt: volt,
              temp: temp,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let volt_graph = [];
          let temp_graph = [];
          for (let el of for_graph) {
            volt_graph.push(this.checker(el.data.volt));
            temp_graph.push(this.checker(el.data.temp));
          }
          let graphics = {
            "График изменения напряжения": {
              vel_name: "Напряжение",
              graph: volt_graph,
            },
            "График изменения температуры": {
              vel_name: "Температура",
              graph: temp_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          };
        } else if (type == "ROSSMA IIOT-AMS DRY CONTACT") {
          headers = [
            "Значение счетчика между передачами данных",
            "Значение счетчика с момента отправки без обработки",
            "Напряжение в мВ",
            "Температура",
          ];
          let date_times = [];
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let counter = parseInt(varka.slice(1, 4 + 1).join(""), 16);
            let counter_2 = parseInt(varka.slice(5, 12 + 1).join(""), 16);
            let volt = parseInt(varka.slice(13, 14 + 1).join(""), 16);
            let temp = parseInt(varka.slice(15).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              counter: counter,
              counter_2: counter_2,
              volt: volt,
              temp: temp,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let volt_graph = [];
          let temp_graph = [];
          for (let el of for_graph) {
            volt_graph.push(this.checker(el.data.volt));
            temp_graph.push(this.checker(el.data.temp));
          }
          let graphics = {
            "График изменения напряжения": {
              vel_name: "Напряжение",
              graph: volt_graph,
            },
            "График изменения температуры": {
              vel_name: "Температура",
              graph: temp_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          };
        } else if (type == "ROSSMA IIOT-AMS ESD") {
          headers = [
            "Причина сработки",
            "Количество сработок по первому входу между пакетами с данными",
            "Количество сработок по второму входу между пакетами с данными",
            "Количество сработок по третьему входу между пакетами с данными",
            "Количество сработок по четвёртому входу между пакетами с данными",
            "Количество импульсов по 5 каналу между пакетами с данными",
            "Общее количество импульсов по 5 каналу",
            "Количество импульсов по 6 каналу между пакетами с данными",
            "Общее количество импульсов по 6 каналу",
            "Напряжение на батарее в мВ",
            "Температура",
          ];
          let date_times = [];
          for (let packet of data) {
            let varka = [];
            for (var i = 0; i < packet.data.length; i += 2) {
              varka.push(packet.data.substr(i, 2).toLocaleLowerCase());
            }
            let m = parseInt(varka.slice(1, 2).join(""), 16).toString(2);
            while (m.length != 8) {
              m = "0" + m;
            }
            let state = "";
            if (m[0] == "1") {
              state = "Датчик наклона";
            } else if (m[1] == 1) {
              state = "Датчик удара";
            } else if (m[2] == 1) {
              state = "Датчик удара 2";
            } else if (m[3] == 1) {
              state = "Звуковой датчик удара";
            }
            let channel_1 = parseInt(varka.slice(2, 3 + 1).join(""), 16);
            let channel_2 = parseInt(varka.slice(4, 5 + 1).join(""), 16);
            let channel_3 = parseInt(varka.slice(6, 7 + 1).join(""), 16);
            let channel_4 = parseInt(varka.slice(8, 9 + 1).join(""), 16);
            let imp_5 = parseInt(varka.slice(10, 13 + 1).join(""), 16);
            let all_imp_5 = parseInt(varka.slice(14, 21 + 1).join(""), 16);
            let imp_6 = parseInt(varka.slice(22, 25 + 1).join(""), 16);
            let all_imp_6 = parseInt(varka.slice(26, 33 + 1).join(""), 16);
            let volt = parseInt(varka.slice(34, 35 + 1).join(""), 16);
            let temp = parseInt(varka.slice(36).join(""), 16);
            let date_time = new Date(packet.ts); /////////////////////////////////
            date_times.push(date_time.toLocaleString());
            let promezh = {
              state: state,
              channel_1: channel_1,
              channel_2: channel_2,
              channel_3: channel_3,
              channel_4: channel_4,
              imp_5: imp_5,
              all_imp_5: all_imp_5,
              imp_6: imp_6,
              all_imp_6: all_imp_6,
              volt: volt,
              temp: temp,
            };
            result.push({
              data: promezh,
              date_time: packet.ts,
            });
          }
          let for_graph = Array.from(result).reverse();
          let ts_for_graph = Array.from(date_times).reverse();
          let volt_graph = [];
          let temp_graph = [];
          for (let el of for_graph) {
            volt_graph.push(this.checker(el.data.volt));
            temp_graph.push(this.checker(el.data.temp));
          }
          let graphics = {
            "График изменения напряжения": {
              vel_name: "Напряжение",
              graph: volt_graph,
            },
            "График изменения температуры": {
              vel_name: "Температура",
              graph: temp_graph,
            },
          };
          let graphs = [];
          for (const [key, graph] of Object.entries(graphics)) {
            graphs.push({
              series: [
                {
                  name: graph.vel_name,
                  data: graph.graph,
                },
              ],
              chartOptions: {
                title: { text: key },
                colors:
                  graph.vel_name == "Температура"
                    ? ["#ff0003"]
                    : graph.vel_name == "Напряжение"
                    ? ["#0000ff"]
                    : graph.vel_name == "Ток"
                    ? ["#ff00ff"]
                    : ["#ffffff"],
                xaxis: {
                  categories: ts_for_graph,
                },
              },
            });
          }
          this.graphs = graphs;
          final_result = {
            headers: headers,
            data: result,
            graphs: graphs,
          };
        } else if (type == "Неизвестный тип") {
          this.error = true;
        }
        console.log(this.graphs);
        return final_result;
      } catch (e) {
        type = "??";
      }
      this.type = type;
    },

    get_type(first_packet) {
      let packet = [];
      for (var i = 0; i < first_packet.length; i += 2) {
        packet.push(first_packet.substr(i, 2).toLocaleLowerCase());
      }
      if (packet.length == 10 && first_packet.length % 2 == 0) {
        return "ROSSMA IIOT-AMS ANALOG (multichannel)";
      } else if (
        packet.length == 6 &&
        first_packet.length % 2 == 0 &&
        packet[0] == "dd"
      ) {
        return "ROSSMA IIOT-AMS ANALOG (single channel)";
      } else if (packet.length == 37 && first_packet.length % 2 == 0) {
        return "ROSSMA IIOT-AMS ESD";
      } else if (packet.length == 16 && first_packet.length % 2 == 0) {
        return "ROSSMA IIOT-AMS DRY CONTACT";
      } else if (
        (packet.length == 7 || packet.length == 6) &&
        first_packet.length % 2 == 0 &&
        (packet[0] == "cc" || packet[0] == "a1")
      ) {
        return "ROSSMA IIOT-AMS DRY CONTACT RELAY";
      } else if (
        packet.length == 44 &&
        packet[0] == "cc" &&
        first_packet.length % 2 == 0
      ) {
        return "ROSSMA IIOT-AMS TILT COUNTER";
      } else if (
        packet.length == 44 &&
        packet[0] == "01" &&
        first_packet.length % 2 == 0
      ) {
        if (packet[1] == "10") {
          return "Квант-1";
        } else if (packet[1] == "11") {
          return "Каскад 2-200";
        } else if (packet[1] == "1c") {
          return "Modbus 3";
        }
      } else {
        return "Неизвестный тип";
      }
    },
    checker(el) {
      return !isNaN(el) ? el : 0;
    },
  },
  watch: {
    date() {
      var today = new Date();
      today.setHours(0);
      today.setMinutes(0);
      today.setSeconds(0);
      today.setMilliseconds(0);
      if (this.date == "today") {
        this.date_filter_down = today;
      } else if (this.date == "week") {
        this.date_filter_down = today.setDate(today.getDate() - 7);
      } else if (this.date == "month") {
        this.date_filter_down = today.setMonth(today.getMonth() - 1);
      } else if (this.date == "three_month") {
        this.date_filter_down = today.setMonth(today.getMonth() - 3);
      } else if (this.date == "half_year") {
        this.date_filter_down = today.setMonth(today.getMonth() - 6);
      } else if (this.date == "year") {
        this.date_filter_down = today.setYear(today.getYear() - 1);
      } else if (this.date == "all") {
        this.date_filter_down = 0;
      }
    },
    date_graph() {
      this.filter_data();
    },
  },
};
</script>

